.container {
    display: flex;
    flex-direction: column;

    height: 100vh;

    opacity: 0;
    transition: opacity 0.5s ease-in-out;

    > .display {
        flex-grow: 1;
        
        display: flex;
        flex-direction: column;
        justify-content: flex-end;

        background-size: cover;
        background-position: center;

        > img.characterImage {
            width: 80%;
            align-self: center;

            // For desktop
            @media screen and (min-width: 768px) {
                width: 500px;
            }
        }

        > .option {
            position: absolute;
            
            width: 100%;
            
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;

            > .menu {

                > p {
                    background-color: bisque;
    
                    padding: 8px 24px;
    
                    border-radius: 12px;

                    text-align: center;

                    cursor: pointer;
                    
                    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.5);
                }
            }
        }
    }

    > .chat {
        display: flex;
        flex-direction: column;
        gap: 12px;

        padding: 24px;

        background-color: bisque;

        height: 120px;

        box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.5);

        > .name {
            margin: 0;
            color: cadetblue;
            font-size: 24px;
        }

        > .text {
            margin: 0;
        }

        > .heart {
            position: absolute;
            bottom: 0;
            right: 12px;
        }
    }
}