@import './Button.scss';
@import './Text.scss';

.main {
    margin: 24px;

    .buttonRefresh {
        @extend .button;
    }

    .buttonFixData {
        @extend .button;
    }
    
    .recordTable {
        border-collapse: collapse;
        width: 100%;
        margin-bottom: 24px;
    
        th, td {
            padding: 8px;
            text-align: left;
            border-bottom: 1px solid #ddd;
        }
    
        tr:nth-child(even) {
            background-color: #f2f2f2;
        }
    }
}
