.mainMenu {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 12px;

    height: 100vh;

    background-color: cadetblue;

    transition: opacity 0.5s ease-in-out;

    background-image: url('./../image/menu-background.webp');
    background-position: center;
    background-size: cover;

    overflow: none;

    > img.logo {
        width: 50%;
    }

    > .startButton {
        margin-top: 24px;
        width: 80%;
        
        padding: 12px;
        
        background-color: white;

        font-size: 24px;
        
        border: none;

        box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.5);

        border-radius: 12px;

        cursor: pointer;

        &:hover {
            filter: brightness(0.9);
        }
    }

    > .skipIntroButton {
        align-self: flex-end;
        margin-right: 10%;
        width: 50%;
        
        padding: 12px;
        
        background-color: bisque;

        font-size: 16px;
        
        border: none;

        box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.5);

        border-radius: 12px;

        cursor: pointer;

        &:hover {
            filter: brightness(0.9);
        }
    }
}
.sceneIntro {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    height: 100vh;

    > div {
        opacity: 0;
        transition: opacity 0.5s ease-in-out;

        display: flex;
        justify-content: center;
        align-items: center;
        
        padding: 24px;

        // Intro
        &.introText {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            
            font-size: 24px;
            padding: 24px;
        }

        // Hidden class
        &.hidden {
            display: none;
        }

        .reactJsLogo {
            width: 100%;
        }
    }
}